<template>
    <b-container class="bv-example-row">
        <br/>
        <h1>
            <i class="fas fa-cogs"></i>
            Dashboard
        </h1>
        <br/>
        <b-row>
            <b-col>
                <b-card
                bg-variant="primary"
                text-variant="white"
                header="Pending Tutor Request"
                class="text-center"
                >
                <div class="text-center">
                    <b-button variant="light">
                        Requests <b-badge variant="danger">{{requestCount}}</b-badge>
                    </b-button>
                </div>
                </b-card>
            </b-col>
            <b-col>
                 <b-card bg-variant="info"
                text-variant="white"
                header="New Applications"
                class="text-center">
                <div class="text-center">
                    <b-button variant="light">
                        Applications <b-badge variant="danger">{{newAppCount}}</b-badge>
                    </b-button>
                    <hr/>
                      <b-button variant="light">
                        Awaiting Interview <b-badge variant="danger">{{interCount}}</b-badge>
                    </b-button>
                </div>
            
        </b-card>
            </b-col>
            <b-col>
                <b-card bg-variant="success"
                text-variant="white"
                header="Tutor Schedules"
                class="text-center">
              <div class="text-center">
                    <b-button variant="light">
                        Schedules <b-badge variant="danger">{{scheduleCount}}</b-badge>
                    </b-button>
                </div>
        </b-card>
            </b-col>
            <b-col>  <b-card bg-variant="danger"
                text-variant="white"
                header="Tutors"
                class="text-center">
              <div class="text-center">
                    <b-button variant="light">
                        Total Tutors <b-badge variant="danger">{{tutorCount}}</b-badge>
                    </b-button>
                </div>
        </b-card>
        </b-col>
        </b-row>
        <br/>
          <div>
                <b-table :sort-by.sync="sortBy"
                        :sort-desc.sync="sortDesc"
                        :items="tutorRequest"
                        :fields="fields"
                        responsive>
                        
                </b-table>
            
        </div>
         <br/>
    </b-container>
       
</template>
<script>
import firebase from "firebase";
import { db } from "../main";

export default {
    data() {
        return {
            sortBy: 'fullname',
            sortDesc: false,
            requestCount: '',
            newAppCount: '',
            interCount:'',
            scheduleCount: '',
            tutorCount:'',
            fields: [
                { key: 'full_name', sortable: true },
                { key: 'phone', sortable: true },
                { key: 'email', sortable: true },
                { key: 'address', sortable: true },
                { key: 'area', sortable: true },
                { key: 'region', sortable: true },
                { key: 'status', sortable: true }
            ],
            tutorRequest: [],
        }
    },
    created() {
        let stat = 'pending'
        db.collection('requests').where('status','==', stat).get().then(snapShot => {
            this.requestCount = snapShot.size;
        })

        let stats = 'confirmed'
         db.collection('tutors').where('status','>', stats).get().then(snapShot => {
            this.newAppCount = snapShot.size;
        })

        db.collection('tutors').where('status','<', stats).get().then(snapShot => {
            this.interCount = snapShot.size;   
        })

          db.collection('tutors').where('status','==', 'confirmed').get().then(snapShot => {
            this.tutorCount = snapShot.size;   
        })

        db.collection('engagements').get().then(snapShot => {
            this.scheduleCount = snapShot.size;  
        })

        db.collection('requests').where('status','==', 'pending').get().then(snapShot => {
          snapShot.forEach((doc) => {
              const data = {
                  'address': doc.data().address,
                  'area':doc.data().area,
                  'email': doc.data().email,
                  'full_name':doc.data().fullname,
                  'phone':doc.data().phone,
                  'region':doc.data().region,
                  'date':doc.data().requested_at,
                  'status':doc.data().status
              }
              this.tutorRequest.push(data);
            //console.log(this.tutorRequest)
          })  
        })

        window.onload = function() {
        if (!window.location.hash) {
            window.location = window.location + '#loaded';
            window.location.reload();
        }
    }
    }
}
</script>
<style scoped>

</style>


