<template>
  <b-navbar toggleable="lg" type="dark" variant="danger">
  <b-navbar-toggle target="nav_collapse"></b-navbar-toggle>
  <b-navbar-brand href="#" to="/dashboard">Admin Panel</b-navbar-brand>
  <b-collapse is-nav id="nav_collapse">
    <b-navbar-nav>
      <b-nav-item v-if="isLoggedIn" href="#" to="/tutors-requests">Tutor Request</b-nav-item>
      <b-nav-item v-if="isLoggedIn" href="#">Tutor Applications</b-nav-item>
      <b-nav-item v-if="isLoggedIn" href="#">Registered Tutors</b-nav-item>
      <b-nav-item v-if="isLoggedIn" href="#" to="/tutors-schedule">Tutor Schedules</b-nav-item>
    </b-navbar-nav>

    <!-- Right aligned nav items -->
    <b-navbar-nav class="ml-auto">
       <b-button v-if="isLoggedIn" variant="light">
            <span>
              <i class="fas fa-user-circle"></i>
              My Account
            </span>
           
      </b-button>
       |
      <b-button v-if="isLoggedIn" @click="logout" variant="light">
            <span>
              <i class="fas fa-sign-out-alt"></i>
              Logout
            </span>
      </b-button>

          <b-button v-if="!isLoggedIn" to="/" variant="light">
            <span>
              <i class="fas fa-sign-in-alt"></i>
              Login
            </span>
      </b-button>
        
         
    </b-navbar-nav>

  </b-collapse>
</b-navbar>
</template>
<script>
import firebase from 'firebase';
export default {
    data() {
        return {
            drawer:false,
            isLoggedIn: false,
            currentUser: false,
        }
    },
    methods: {
      logout() {
        firebase.auth().signOut().then(user =>{
          this.$router.replace('/');
          
          this.isLoggedIn = false;
          this.currentUser = false;
        })
      }
    },
    created() {
      if(firebase.auth().currentUser){
        this.isLoggedIn = true;
        this.currentUser = firebase.auth().currentUser.email;
      }
    }
}
</script>
