<template>
    <div class="login-form">
    <form @submit.prevent = "logIn">
		<div class="avatar">
			<img src="../assets/logo.png" alt="Avatar">
		</div>
       <br/>
       <br/>
        <br/>
         <br/>
          <br/>
           <br/>
        <div class="form-group">
        	<input type="text" class="form-control" v-model="email" name="email" placeholder="Email" required>
        </div>
		<div class="form-group">
            <input type="password" class="form-control" v-model="password" name="password" placeholder="Password" required>
        </div>        
        <div class="form-group">
            <button type="submit" class="btn btn-primary btn-lg btn-block">Login</button>
			
        </div>
		<div class="clearfix">
            <router-link class="text-center" to="/forget-password">
          		<p class="text-center"><a>Forgot Password?</a></p>
      		</router-link>        
        </div>
    </form>
      <div class="shadow" v-if="loading">
         <img class="centeredImage" src="../assets/loader.gif">      </div>
    </div>
</template>
<script>
import firebase from 'firebase';
import { db } from '../main.js';        
export default {
    data() {
        return{
            email: '',
			password:'',
			loading:false,
        }
    },

    methods: {
        logIn() {
            this.loading= true;
             db.collection('admin').where('email','==',this.email).get()
            .then((snapShot) => {
                if (snapShot.empty) {
                    alert('Invalid User, Please try again');
                    this.$router.go({path: this.$router.path});
                }else{
                    firebase.auth().signInWithEmailAndPassword(this.email, this.password)
                    .then(user => {
                            this.loading = false;
                            this.$router.push('/dashboard');
                        },
                        err => {
                        alert(err.message);
                        }
                    )
                }
            },
            err=>{
                alert('Not a valid user '+ err.message);
            }
            )
        }
},
    created(){
        if(firebase.auth().currentUser) {
            this.$router.replace('/dashboard');
        }

          window.onload = function() {
            if (!window.location.hash) {
                window.location = window.location + '#loaded';
                window.location.reload();
            }
        }
       
    }
}
</script>
<style scoped>
.class{
		color: #fff;
		background: red;
	}
	.form-control {
        min-height: 41px;
		background: #fff;
		box-shadow: none !important;
		border-color: #e3e3e3;
	}
	.form-control:focus {
		border-color: #70c5c0;
	}
    .form-control, .btn {        
        border-radius: 2px;
    }
	.login-form {
		width: 350px;
		margin: 0 auto;
		padding: 100px 0 30px;		
	}
	.login-form form {
		color: #7a7a7a;
		border-radius: 2px;
    	margin-bottom: 15px;
        font-size: 13px;
        background: #ececec;
        box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
        padding: 30px;	
        position: relative;	
    }
	.login-form h2 {
		font-size: 22px;
        margin: 35px 0 25px;
    }
	.login-form .avatar {
		position: absolute;
		margin: 0 auto;
		left: 0;
		right: 0;
		top: -50px;
		width: 130px;
		height: 130px;
		border-radius: 50%;
		z-index: 9;
		background: white;
		padding: 15px;
		box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.1);
	}
	.login-form .avatar img {
		width: 100%;
	}	
    .login-form input[type="checkbox"] {
        margin-top: 2px;
    }
    .login-form .btn {        
        font-size: 16px;
        font-weight: bold;
		background: #70c5c0;
		border: none;
		margin-bottom: 20px;
    }
	.login-form .btn:hover, .login-form .btn:focus {
		background: #50b8b3;
        outline: none !important;
	}    
	.login-form a {
		color: #fff;
		text-decoration: underline;
	}
	.login-form a:hover {
		text-decoration: none;
	}
	.login-form form a {
		color: #7a7a7a;
		text-decoration: none;
	}
	.login-form form a:hover {
		text-decoration: underline;
	}
.center {
    display: block;
    margin-left: auto;
    margin-right: auto;
    
}
.centeredImage {
  position:relative;
    max-width:100%;
    max-height:100%;
    margin-top:-50%;
    margin-left:-50%;
}
div.shadow {
    position:absolute;
    max-width:45%;
    max-height:45%;
    top:50%;
    left:50%;
   
}
</style>

