import Vue from 'vue';
import firebase from 'firebase';
import Router from 'vue-router';
import Dashboard from './components/Dashboard.vue';
import Tutors from './components/Tutors.vue';
import Login from './components/Login.vue';
import TutorRequests from './components/TutorRequest.vue';
import Assign from './components/Assign.vue';
import TutorSchedule from './components/TutorSchedule.vue';
import ScheduleDetails from './components/ScheduleDetails.vue';

Vue.use(Router);

const router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'Login',
      component:Login,
      meta: {
        requireAuth: true,
      }
    },
        {
      path: '/dashboard',
      name: 'Dashboard',
      component: Dashboard,
      meta: {
        requireGuest: true,
      },
    },
    {
      path: '/tutors',
      name: 'Tutors',
      component: Tutors,
      meta: {
        requireAuth: true,
      },
    },
     {
      path: '/schedule-details/:request_id',
      name: 'ScheduleDetails',
      component: ScheduleDetails,
      meta: {
        requireAuth: true,
      },
    },
  {
    path: '/tutors-schedule',
    name: 'TutorsSchedule',
    component: TutorSchedule,
    meta: {
      requireAuth: true,
    },
  },
       {
         path: '/assign/:request_id',
         name: 'assign-tutor',
         component: Assign,
         meta: {
           requireAuth: true,
         },
       },
    {
      path: '/tutors-requests',
      name: 'TutorRequests',
      component: TutorRequests,
      meta: {
        requireAuth: true,
      },
    },

  ],
});

router.beforeEach((to, from, next) => {
  //check for requireAuth Guard
  if (to.matched.some(record => record.meta.requiresAuth)) {
    //check if NOT logged in
    if (!firebase.auth().currentUser) {
      //Go to login
      next({
        path: "Login",
        query: {
          redirect: to.fullPath
        }
      });
    } else {
      //proceed to route
      next();
    }
  } else if (to.matched.some(record => record.meta.requiresGuest)) {
    //check if logged in
    if (firebase.auth().currentUser) {
      //Go to login
      next({
        path: "Dashboard",
        query: {
          redirect: to.fullPath
        }
      });
    } else {
      //proceed to route
      next();
    }
  } else {
    next();
  }
});

export default router;