<template>
        <b-container >
        <br/>
        <h2>
            <i class="far fa-newspaper"></i>
            Request Details
        </h2>
        <hr/>
        <b-card  bg-variant="light" title="">
         <b-container class="bv-example-row">
             <b-row>
                <b-col sm="4">
                    <div class="pricing card-deck flex-column flex-md-row mb-3">
                    <div class="card card-pricing popular shadow text-center px-3 mb-4" v-for="(req of request">
                        <span class="h6 w-60 mx-auto px-4 py-1 rounded-bottom bg-danger text-white shadow-sm">{{req.id}}</span>
                        <div class="bg-transparent card-header pt-4 border-0">
                        <h1 class="h1 font-weight-normal text-info text-center mb-0" data-pricing-value="30"><span class="h6 text-muted ml-2">{{req.full_name}}</span></h1>
                        </div>
                        <div class="card-body pt-0">
                        <ul class="list-unstyled mb-4">
                            <li>{{req.phone}}</li>
                            <li>{{req.email}}</li>
                            <li>{{req.address}}</li>
                            <li>{{req.area}}</li>
                            <li>{{req.region}}</li>
                            <li>{{req.date}}</li>
                        </ul>
                        <button type="button" @click="assign" class="btn btn-info mb-3">
                        <i class="fas fa-check-circle"></i>
                        View
                        </button>

                        <button type="button" @click="deleteRequest" class="btn btn-danger mb-3">
                            <i class="fas fa-trash-alt"></i>
                            Delete
                        </button>
                        </div>
                </div>
                    </div>
                 </b-col>
                 <b-col sm="8" v-if="showPart">
                     <form @submit.prevent="beforeSubmit">
                    <b-card>
                        <b-row>
                            <b-col>
                                <label>Select Tutor</label>
                                <b-form-select v-model="selectedTutor" :class="{ 'is-invalid': submitted && errors.has('Tutor') }" v-validate="'required'" name="Tutor" :options="tutors" class="mb-3" />
                                <div>{{selectedTutor}}</div>
                                <div v-if="submitted && errors.has('Tutor')" class="invalid-feedback">{{ errors.first('Tutor') }}</div> 
                            </b-col>
                             <b-col>
                                <label>Amount</label>
                                <input v-model="eng.amount" :class="{ 'is-invalid': submitted && errors.has('Amount') }" v-validate="'required'" class="form-control" type="number" step="01" name="Amount">
                                <div v-if="submitted && errors.has('Amount')" class="invalid-feedback">{{ errors.first('Amount') }}</div> 
                            </b-col>
                        </b-row>
                        <hr/>
                        <b-row>
                            <b-col>
                            <b-card>
                                <label>Days</label>
                                 <b-form-checkbox-group :class="{ 'is-invalid': submitted && errors.has('Days') }" v-validate="'required'" v-model="eng.days" name="Days" :options="days" />
                                 <div v-if="submitted && errors.has('Days')" class="invalid-feedback">{{ errors.first('Days') }}</div> 
                                 <hr/>
                                <div class="row">
                                <div class="col">
                                 <label>Start Time</label>
                                <input v-model="eng.startTime" :class="{ 'is-invalid': submitted && errors.has('Days') }" v-validate="'required'" class="form-control" type="time" name="Start Time">
                                <div v-if="submitted && errors.has('Start Time')" class="invalid-feedback">{{ errors.first('Start Time') }}</div> 
                                </div>
                                <div class="col">
                                 <label>End Time</label>
                                <input v-model="eng.endTime" :class="{ 'is-invalid': submitted && errors.has('End Time') }" v-validate="'required'" class="form-control" type="time" name="End Time">
                                <div v-if="submitted && errors.has('End Time')" class="invalid-feedback">{{ errors.first('End Time') }}</div> 
                                </div>
                                 </div>
 <hr/>
                                <div class="row">
                                <div class="col">
                                 <label>Start Date</label>
                                <input v-model="eng.startDate" :class="{ 'is-invalid': submitted && errors.has('Start Date') }" v-validate="'required'" class="form-control" type="date" name="Start Date">
                                <div v-if="submitted && errors.has('Start Date')" class="invalid-feedback">{{ errors.first('Start Date') }}</div> 
                                </div>
                                <div class="col">
                                 <label>End Date</label>
                                <input v-model="eng.endDate" :class="{ 'is-invalid': submitted && errors.has('End Date') }" v-validate="'required'" class="form-control" type="date" name="End Date">
                                <div v-if="submitted && errors.has('End Date')" class="invalid-feedback">{{ errors.first('End Date') }}</div> 
                                </div>
                                 </div>
                                  <hr/>
                                <label>Levels</label>
                                 <b-form-checkbox-group v-model="eng.levels" name="Levels" :options="levels" />
                                  <hr/>
                                  <label>Subjects</label>
                                 <b-form-checkbox-group v-model="eng.subjects" name="Subjects" :options="subjects" />
                                 <hr/>
                                 <label>Description</label>
                                <b-form-textarea id="textarea1"
                                :class="{ 'is-invalid': submitted && errors.has('Description') }" v-validate="'required'"
                                v-model="eng.description"
                                placeholder="Description"
                                name="Description"
                                :rows="3"
                                :max-rows="6">
                                </b-form-textarea>
                                <div v-if="submitted && errors.has('Description')" class="invalid-feedback">{{ errors.first('Description') }}</div> 
                            </b-card>
                            </b-col>
                        </b-row>
                        <br/>
                        <button type="submit" size="lg" class="btn btn-block btn-info">Assign Tutor</button>
                    </b-card>
                    
                    </form>
                 </b-col>
             </b-row>
             
         </b-container>
        </b-card>
        <b-row>
        
        </b-row>
        <hr/>
    <div class="shadow1" v-if="loading">
         <img class="centeredImage" src="../assets/loader.gif" />      
     </div>
    </b-container>
</template>
<script>
import firebase from 'firebase';
import { db } from '../main.js';
export default {
    data() {
        return{
            id:'',
            submitted: false,
            eng: {
                amount: '',
                days:[],
                endDate:'',
                endTime:'',
                id:'',
                levels:[],
                startDate:'',
                startTime:'',
                subjects:[],
                tutorId:'',
                description:'',                
            },
            request:[],
            loading:false,
            nAddress:'',
            nName:'',
            nArea:'',
            nRegion:'',
            nRid:'',
            nPhone: '',
            tutors:[],
            showPart:false,
            time:[
                {text:'8:00am', value:'8:00am'},
                {text:'8:30am', value:'8:30am'},
                {text:'9:00am', value:'9:00am'},
                {text:'9:30am', value:'9:30am'},
                {text:'10:00am', value:'10:00am'},
                {text:'8:00am', value:'8:00am'},
                {text:'8:00am', value:'8:00am'},
                {text:'8:00am', value:'8:00am'},
            ],
            selectedTutor: null,
            subjects: [
                {text: 'English', value: 'Engliss'},
                {text: 'Mathematics', value: 'Mathematics'},
                {text: 'Integrated Science', value: 'Integrated Science'},
                {text: 'Social Studies', value: 'Social Studies'},
                {text: 'Citizenship Education', value: 'Citizenship Education'},
                {text: 'BDT', value: 'BDT'}
            ],
            days: [
                {text: 'Sunday', value:'Sunday'},
                {text: 'Monday', value:'Monday'},
                {text: 'Tuesday', value:'Tuesday'},
                {text: 'Wednesday', value:'Wednesday'},
                {text: 'Thursday', value:'Thursday'},
                {text: 'Friday', value:'Friday'},
                {text: 'Saturday', value:'Saturday'},
            ],
              levels: [
                {text: 'Nursery', value: 'Nursery'},
                {text: 'Lower Primary', value: 'Lower Primary'},
                {text: 'Upper Primary', value: 'Upper Primary'},
                {text: 'Junior High', value: 'Junior High'},
                {text: 'Senior High', value: 'Senior High'},
                {text: 'Under Graduate', value: 'Under Graduate'}
              ]
        }
    },
    created() {
        //this.id = this.$route.params.request_id;
     
        db.collection('requests').where('rid','==', this.$route.params.request_id).get().then(snapShot => {
          snapShot.forEach((doc) => {
              const data = {
                  'address': doc.data().address,
                  'area':doc.data().area,
                  'email': doc.data().email,
                  'full_name':doc.data().fullname,
                  'phone':doc.data().phone,
                  'region':doc.data().region,
                  'date':doc.data().requested_at,
                  'status':doc.data().status,
                  'id': doc.data().rid
              }
              this.nName = doc.data().fullname;
              this.nAddress = doc.data().address;
              this.nArea = doc.data().area;
              this.nRegion = doc.data().region
              this.nRid= doc.data().rid;
              this.Nphone = doc.data().phone

              this.request.push(data);
              
            //console.log(JSON.stringify(this.tutorRequest) )
          })  
        })
        db.collection('tutors').where('available','==', 'yes').get().then(snapShot => {
            snapShot.forEach((doc) => {
                const data = {
                    'value':doc.data().userId,
                    'text': doc.data().firstname + ' ' + doc.data().lastname
                }
                this.tutors.push(data);
            })
        })
       
    },

  methods: {
      deleteRequest(){
        if(confirm ('Are you sure?')) {
          db.collection('requests').where('rid', '==', this.$route.params.request_id).get().then((querySnapshot) => {
            querySnapshot.forEach((doc) => {
              doc.ref.delete();
              this.$router.push('/tutors-requests');
            })
          })}
      },
      assign(){
          this.showPart = !this.showPart;
      },
      beforeSubmit() {
        this.submitted = true;
        
        this.$validator.validate().then(valid => {
            if (valid) {
              this.loading = true;
              db.collection('engagements').add({
              address: this.nAddress,
              amount: this.eng.amount,
              client_name: this.nName,
              days: this.eng.days,
              end: this.eng.endDate,
              endtime: this.eng.endTime,
              id: this.nRid,
              levels: this.eng.levels,
              location: this.nArea,
              region: this.nRegion,
              start: this.eng.startDate,
              starttime: this.eng.startTime,
              subjects: this.eng.subjects,
              tutorId: this.selectedTutor,
              description: this.eng.description,
              client_phone: this.nPhone,
          }).then(ref => {
              db.collection('requests').where('rid','==',this.nRid).get().then((snap) => {
                  snap.forEach((doc) => {
                      doc.ref.update({status:'assigned'})
                  })
              })
                db.collection('tutors').where('userId','==',this.selectedTutor).get().then((snap1) => {
                  snap1.forEach((doc1) => {
                      doc1.ref.update({available:'no'})
                  })
              })
              this.loading = false;
              alert('Tutor Assigned Successfully');
              this.$router.push('/tutors-requests');
          }).catch(err => {
                alert(err);
        });
        }
        })
      
      }
  }

}
</script>
<style scoped>
    .card-pricing.popular {
    z-index: 1;
    border: 3px solid red;
}
.card-pricing .list-unstyled li {
    padding: .5rem 0;
    color: #6c757d;
}

table caption {
	padding: .5em 0;
}

@media screen and (max-width: 767px) {
  table caption {
    border-bottom: 1px solid #ddd;
  }
}

.p {
  text-align: center;
  padding-top: 140px;
  font-size: 14px;
}



.centeredImage {
  position:relative;
    max-width:100%;
    max-height:100%;
    margin-top:-50%;
    margin-left:-50%;
}
div.shadow1 {
    position:absolute;
    max-width:45%;
    max-height:45%;
    top:50%;
    left:50%;
   
}
</style>
